@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@300;400;500;700&display=swap);
* {
	box-sizing: border-box;
	font-size: 62.5%;
}

html {
	font-family: 'Spartan', sans-serif;
}

body {
	margin: 0;
	padding: 0;
	background-color: hsl(180, 52%, 96%);
	height: 100vh;
}

.Header_header__3xkaR {
