}

.ItemInfo_contact__hVIny {
	color: hsl(180, 8%, 52%);
	font-size: 1.4rem;
	margin: 0 1.4rem;
	position: relative;
}

.ItemInfo_contact__hVIny:first-child {
	margin-left: 0;
}

.ItemInfo_contact__hVIny:last-child {
	margin-right: 0;
}

.ItemInfo_contact__hVIny::after {
	content: '';
	position: absolute;
	border: 2px solid hsl(180, 8%, 52%);
	border-radius: 50%;
	top: 50%;
	right: -1.4rem;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.ItemInfo_contact__hVIny:last-child::after {
	display: none;
}

@media screen and (max-width: 850px) {
	.ItemInfo_info__ufi08 {
		flex-direction: column;
		border-bottom: 1px solid hsl(180, 8%, 52%);
		padding: 4rem 0 2rem;
	}

	.ItemInfo_info-img__29k2Q {
		position: absolute;
		top: 0;
		left: 4rem;
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
	}

	.ItemInfo_info-img__29k2Q img {
		width: 65%;
	}
}

